@use 'base/variables' as v;
@use 'base/mixins' as m;

.banner{
    background: rgb(0,0,0);
    background: -moz-linear-gradient(196deg, rgba(0,0,0,1) 0%, rgba(185,147,49,1) 100%);
    background: -webkit-linear-gradient(196deg, rgba(0,0,0,1) 0%, rgba(185,147,49,1) 100%);
    background: linear-gradient(196deg, rgba(0,0,0,1) 0%, rgba(185,147,49,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#000000",endColorstr="#b99331",GradientType=1);
    padding: 5rem 1rem 0 1rem;
    display: flex;
    justify-content: space-between;

    @include m.computadora{
        padding: 20rem 0 0 5rem;
    }
    

    &__content{
        text-align: left;
        padding-bottom: 3rem;

        h2{
            text-align: left;
            color: v.$blanco;
            font-size: 4rem;

            @include m.computadora{
                font-size: 9rem;
            }
        }

        p{
            color: v.$blanco;
            font-size: 1.8rem;

            @include m.tablet{
                font-size: 2.2rem;
            }

            span{
                background-color: v.$dorado;
                padding-left: 1rem;
                padding-right: 1rem;
                border-radius: 1rem;
                margin-left: 1rem;                
            }
        }

        a{
            padding: 2rem;
            margin-top: 1.5rem;
            border-radius: .5rem;
            font-size: 1.8rem;
            @include m.boton(v.$negro, v.$blanco, v.$blanco, v.$negro, inline-block)
        }
    }

    &__image{
        z-index: 1;
        position: relative;
        display: none;

        @include m.computadora{
            display: flex;
        }
        &-fundador{
            width: 70rem;
        }

        &-circle{
            position: absolute;
            top: -5rem;
            left: 2rem;
            z-index: -1;
            width: 65rem;
        }
    }
}

.why-us{
    text-align: center;
    padding: 5rem 3rem;
    background-color: v.$fondo_claro;

    &__title{
        font-size: 4.5rem;
    }

    &__text{
        font-size: 1.7rem;
        width: 70%;
        max-width: 70rem;
        margin: 3rem auto;
        color: v.$gris;
    }

    &__grid{
        
        display: block;
        margin: 0 auto;
        width: 80%;

        @include m.tablet{
            width: 90%;
            @include m.grid(2, 3rem);
        }

        @include m.computadora{
            width: 90%;
            @include m.grid(4, 3rem);
        }
    }

    .razon{
        padding: 3rem;
        border-radius: 1rem;
        background-color: v.$blanco;
        margin-bottom: 3rem;
        border-bottom: .3rem solid v.$dorado;

        @include m.computadora{
            margin-bottom: 0;
        }

        &__sub{
            border-top: .2rem solid v.$dorado;
            border-style: dashed none none none;
            text-align: left;
            font-size: 2rem;
            color: v.$gris;
            margin-top: 1rem;
            padding-top: 1rem;
        }

        &__title{
            text-align: left;
            font-size: 2.8rem;
        }
    }
}

.about{
    display: block;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    width: 90%;
    gap: 3rem;
    padding: 10rem 0;

    @include m.computadora{
        display: flex;
    }

    &__grid{
        display: block;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(3, 1fr);
        gap: 2rem;
        width: 100%;
        margin-bottom: 2rem;

        @include m.tablet{
            display: grid;
        }

        @include m.computadora{
            width: 50%;
            margin-bottom: 0;
        }

        .foto-fundador-1{
            width: 100%;
            height: 40rem;
            grid-row: 1 / 3;
            border-radius: 1rem;
            background-image: url('/assets/images/img-1.jpg');
            background-repeat: no-repeat;
            background-position: top center;
            background-size: cover;
            margin-bottom: 2rem;

            @include m.tablet{
                margin-bottom: 0;
                background-position: center center;
            }

        }

        .foto-fundador-2{
            width: 100%;
            height: 40rem;
            grid-column: 2 / 3;
            grid-row: 2 / 4;
            border-radius: 1rem;
            background-image: url('/assets/images/img-2.jpg');
            background-repeat: no-repeat;
            background-position: top center;
            background-size: cover;
            margin-bottom: 2rem;

            @include m.tablet{
                margin-bottom: 0;
                background-position: center center;
            }

        }

        .info-1{
            grid-row: 1 / 2;
            grid-column: 2 / 3;
            padding: 3.5rem 2rem;
            background-color: v.$dorado;
            color: v.$blanco;
            border-radius: 1rem;
            font-size: 8rem;
            margin-bottom: 2rem;
            
            @include m.tablet{
                margin-bottom: 0;
            }

            p{
                color: v.$blanco;
                margin: 0;
                font-weight: v.$bold;
            }
        }

        .info-2{
            grid-row: 3 / 4;
            grid-column: 1 / 2;
            padding: 3.5rem 2rem;
            background-color: v.$dorado;
            color: v.$blanco;
            border-radius: 1rem;
            font-size: 8rem;
            margin-bottom: 2rem;
            
            @include m.tablet{
                margin-bottom: 0;
            }
            
            p{
                color: v.$blanco;
                margin: 0;
                font-weight: v.$bold;
            }
        }
    }

    &__cont{
        width: 100%;

        @include m.computadora{
            width: 50%;
        }

        &-title{
            display: inline-block;
            padding: .5rem 3rem;
            font-size: 1.8rem;
            background-color: v.$dorado;
            color: v.$blanco;
            border-radius: 1rem;
            margin-bottom: 0;
        }

        &-sub{
            font-size: 4.8rem;
            margin-top: 1.5rem;
            font-weight: v.$bold;
        }

        &-btn{
            padding: 2rem;
            margin-top: 1.5rem;
            border-radius: .5rem;
            font-size: 1.8rem;
            @include m.boton(v.$negro, v.$dorado, v.$blanco, v.$blanco, inline-block)
        }
    }
}